@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.App {
  max-width: 860px;
  margin: 10px auto;
}
button {
  display: block;
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  transition: 0.8s ease;
  transition-property: border, background;
}
button:hover {
  background: #c23866;
  border: 2px solid transparent;
}
.cards {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
}
.turns {
  float: right;
  margin: 10px auto;
  display: inline-block;
  position: relative;
  grid-column: 2/4;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 500;
  font-style: italic;
}
.copyright {
  margin: 10px auto;
  color: rgb(189, 179, 179);
  font-size: 20px;
}

.card {
  position: relative;
}
.card img {
  width: 100%;
  display: block;
  border: 2px solid white;
  border-radius: 10px;
}
